import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingModule } from './app-routing.module';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatNativeDateModule } from '@angular/material';
import { MaterialModule } from './material';
import { DragDropModule } from '@angular/cdk/drag-drop';
import { JwtModule } from '@auth0/angular-jwt';
import { TokenInterceptor } from './_interceptor/token.interceptor';

import { AppComponent } from './app.component';
import { NavComponent } from './nav/nav.component';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SignupComponent } from './signup/signup.component';
import { UserService } from './_service/user.service';
import { UserComponent, CreateMapDialog, DeleteMapDialog } from './user/user.component';
import { AdminComponent } from './admin/admin.component';
import { MapComponent, BottomSheetOperationSheet } from './map/map.component';
import { MapGuard } from './_guard/map.guard';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { ProfileComponent, DeleteUserDialog } from './profile/profile.component';
import { CommunityComponent } from './community/community.component';
import { ThemeService } from './_service/theme.service';
import { FilterPipe } from './_pipe/filter.pipe';

export function tokenGetter() {
  return localStorage.getItem('METRO_ACCESS_TOKEN');
}

@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    LoginComponent,
    DashboardComponent,
    SignupComponent,
    UserComponent,
    AdminComponent,
    MapComponent,
    BottomSheetOperationSheet,
    DeleteMapDialog,
    CreateMapDialog,
    PageNotFoundComponent,
    ProfileComponent,
    DeleteUserDialog,
    CommunityComponent,
    FilterPipe
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MaterialModule,
    FormsModule,
    HttpClientModule,
    MatNativeDateModule,
    ReactiveFormsModule,
    DragDropModule,
    JwtModule.forRoot({
      config: {
        tokenGetter: tokenGetter,
        whitelistedDomains: ['localhost:4200'],
        blacklistedRoutes: []
      }
    }),
  ],
  providers: [
    UserService,
    ThemeService,
    MapGuard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent],
  entryComponents: [CreateMapDialog, DeleteMapDialog, DeleteUserDialog, BottomSheetOperationSheet]
})
export class AppModule { }
